import {ChannelsBase} from '../channels/channels-base';
const R = require('ramda');
const Rx = require('rxjs');

export class ChannelUI extends ChannelsBase {
  constructor() {
    super();
    this.props.name = 'UI';
    this.keyEventsLoaded = false;
    this.keyCodeArr = [];
    // this.addKeyEvent(13);
  }
  loadKeyStream() {
    let keyUps = Rx.Observable.fromEvent(document, 'keyup');
    let filterKeys = e => this.keyCodeArr.indexOf(e.keyCode) >= 0;
    this.keyPresses$ = keyUps
      .groupBy(e => e.keyCode)
      .mergeAll()
      .filter(filterKeys)
      .repeat()
      .subscribe(this.onKeyPressed.bind(this));
  }
  addKeyEvent(num) {
    if (this.keyEventsLoaded === false) {
      this.loadKeyStream();
    }
    this.keyEventsLoaded = true;
    this.registerKey(num);
  }
  registerKey(c) {
    this.keyCodeArr.push(c);
  }
  onKeyPressed(evt) {
    console.log('key is ', evt);
  }
  onIncomingObservable(obj) {
    let eqsName =  R.equals(obj.name, this.props.name);
    let dataObj = obsVal => ({data: obj.data, mouse:obsVal});
    let onSuccess = (obj) => obj.observable
      .map(dataObj)
      .subscribe(this.onClick.bind(this));
    let onError = () => {};
    return eqsName === true ? onSuccess(obj) : onError();
  }
  getActionState(val) {
    let mainAction = 'UI_EVENT';
    return val.mouse.type !== undefined ? `${mainAction}_${String(val.mouse.type).toUpperCase()}` : mainAction;
  }

  onClick(val) {
    val['action'] = this.getActionState(val);
    // console.log('btn val ', val);
    this.observer$.next(val);
  }
}



