// import {pullSlashFromPath, removeSlashes, pullMainRoute, pullParams} from '../utils/frp-tools';
// import {pullSlashFromPath} from '../utils/frp-tools';
import {ChannelsBase} from '../channels/channels-base';

const Rx = require('rxjs');
const R = require('ramda');

export class ChannelRoute extends ChannelsBase {
	constructor() {
		super();
		this.props.name = 'ROUTE';
		this.routeCount = 0;
		// this.popupStream$ = this.createPopStateStream();
		this.navToStream$ = new Rx.Subject();
		// this.currentRoute = '';
		this.observer$ = this.navToStream$.map(info => this.onMapNext(info));
		this.initStream();
	}

	initStream() {
		this.firstLoadStream$ = new Rx.BehaviorSubject(
				{data: this.getDataFromWindow(), action: 'CHANNEL_ROUTE_CHANGE_EVENT'});
		this.createPopStateStream();

		this.observer$ = Rx.Observable.merge(this.firstLoadStream$,
				this.navToStream$);
	}

	onMapNext(data, firstLoaded = false) {
		data['action'] = 'CHANNEL_ROUTE_CHANGE_EVENT';
		return data;
	}

	mapRouteData(routeNum, dist) {
	}

	goToRoute(n, d) {
	}

	onObserversCallback(payload) {
		let obj = this.addState(payload.data);
		// obj['data'] = payload.data;
		let routeVal = payload.data.routeVal !== undefined
				? payload.data.routeVal
				: window.location.pathname;
		let windowData = this.getDataFromWindow(routeVal);
		obj['data'] = Object.assign(windowData, payload.data);
		// console.log('observer callback ', data);
		this.sendRouteStream(Object.freeze(obj));
	}

	addState(d = {}) {
		let data = Object.assign({}, d);//  fn(payload.data);
		data['action'] = 'CHANNEL_ROUTE_CHANGE_EVENT';
		return data;
	}

	sendRouteStream(data, changeWindowLoc = true) {
		if (changeWindowLoc === true) {
			this.setWindowLocation(data.routeVal);
		}
		console.log('ROUTE STREAM SEND ', data);
		this.navToStream$.next(Object.freeze(data));
	}

	createPopStateStream() {
		let addHandler = function(handler) {
			window.onpopstate = handler;
		};
		let removeHandler = function() {
			window.onpopstate = function() {};
		};
		console.log('create popsate event from Event pattern', addHandler);
		return Rx.Observable.fromEventPattern(addHandler, removeHandler).
				do((p) => console.log('popstate event from router1 ', p)).
				map((p) => ({
					event: p,
					action: 'CHANNEL_ROUTE_CHANGE_EVENT',
					data: this.getDataFromWindow(p),
				})).
				subscribe((p) => {
					console.log('subscribe is ', p);
					this.sendRouteStream(p, false);
					// this.sendRouteStream(this.addState(d), false);
				});
	}

	getDataFromLocation() {
		// console.log('pop up action get from location ', this.getDataFromWindow(), p);
		return undefined;
	}

	convertRouteToParams(str) {
		const slashRe = /^([\/])?(.*)$/;
		let paramsArr = str.replace(slashRe, '$2').split('/');
		let obj = {};
		let arr = [];
		let spyneConfig = window.Spyne.config ? window.Spyne.config : {};
		let routeConfig = R.path(['channels', 'ROUTE'], spyneConfig);
		if (routeConfig === undefined) {
			return {};
		}
		let topLevelRoute = routeConfig.routes.route;

		let createParamsObj = (str) => {
			let param = topLevelRoute.param;
			let value = topLevelRoute.hasOwnProperty('map')
					? topLevelRoute.map[str]
					: str;
			if (typeof (value) === 'object') {
				topLevelRoute = value.route;
				value = value.map;
			}
			console.log('new create param ', param, value);
			if (value !== undefined) {
				arr.push([param, value]);
			}
			return param;
		};
		paramsArr.forEach(createParamsObj);
		let finalObj = R.fromPairs(arr);
		finalObj['routedParamsArr'] = R.keys(finalObj);
		finalObj['routedParam'] = R.last(arr)[0];
		console.log('new param arr ', str, paramsArr);
		window.theArr1 = arr;
		return finalObj;
	}

	getDataFromWindow(path = window.location.pathname) {
		let str = this.getWindowLocation();
		let routeVal = str;
		let isHash = R.contains('#', window.location.href);
		let pullHashValRE = /(\w+)/;
		let setHashVal = x => {
			x['hashValue'] = R.compose(R.head, R.match(pullHashValRE))(
					window.location.hash);
		};
		const location = window.location;
		const routeCount = this.routeCount;
		const isDeepLink = this.routeCount === 0;
		const paramsObj = this.convertRouteToParams(path);
		console.log('new params obj ', paramsObj);
		let obj = {routeVal, isHash, location, routeCount, isDeepLink};
		obj = Object.assign(paramsObj, obj);
		this.routeCount++;
		if (isHash === true) {
			setHashVal(obj);
		}
		return obj;
	}

	setWindowLocation(val) {
		window.history.pushState({}, '', val);
	}

	getWindowLocation() {
		return window.location.pathname; // pullHashAndSlashFromPath(window.location.hash);
	}
}



