// const R = require('ramda');
import {ChannelsBase} from '../channels/channels-base';
import {pullRouteInfo} from '../utils/frp-tools';
const Rx = require('rxjs');

export class ChannelDOM extends ChannelsBase {
  constructor() {
    super();
    this.observer$ = new Rx.Subject();
    Rx.Observable.fromEvent(window, 'resize')
      .debounceTime(200)
      .subscribe((p) => this.onSubscribeToDomEvents(p));
    this.props.name = 'DOM';
  }
  addMouseWheel() {
    let addHandler = (handler) => { window.onmousewheel = handler; };
    let removeHandler = () => { window.onmousewheel = () => {}; };
    // let oneSecInt = Rx.Observable.interval(1000);
    let mouseWheel$ = Rx.Observable.fromEventPattern(addHandler, removeHandler)
      .debounceTime(150);
    window.mw$ = mouseWheel$;
    mouseWheel$.subscribe((p) => this.onSubscribeToWheelEvents(p));
  }
  addScroll() {
    let addHandler = (handler) => { window.onscroll = handler; };
    let removeHandler = () => { window.onscroll = () => {}; };
    let scroll$ = Rx.Observable.fromEventPattern(addHandler, removeHandler);
    scroll$.subscribe((p) => this.onSubscribeToScroll(p));
  }
  onSubscribeToScroll() {
    let scrollY = window.scrollY;
    let scrollYNegative = scrollY * -1;
    let action = 'SCROLL_EVENT';
    let data = {
      scrollY,
      scrollYNegative,
      action
    };
    this.observer$.next(Object.freeze(data));
  }
  /*
   *  Media queries
   *
  addMediqQueries() {
    this.portraitPhoneQueryStr = '(max-width: 414px) and (min-width: 320px) and (orientation: portrait)';
    let mqlPortraitPhone = window.matchMedia(this.portraitPhoneQueryStr);
    let handlers = (q) => ({
      addHandler: (handler) => q.onchange = handler,
      removeHandler: (handler) => q.onchange = () => {}
    });
    let portraitPhoneHandler = new handlers(mqlPortraitPhone);
    let mqlObserver = new Rx.Observable.fromEventPattern(portraitPhoneHandler.addHandler, portraitPhoneHandler.removeHandler)
      .subscribe((p) => this.onSubscribeToMediqQueries(p));
  }
  */
  onSubscribeToWheelEvents(p) {
    let incrementNum = p.deltaY >= 0 ? 1 : -1;
    let routeInfo = pullRouteInfo();
    let isActive = routeInfo.params >= 1;
    let isHomeBool = routeInfo.routeId === '';
    let paramNum = routeInfo.params;
    let routeId = routeInfo.routeId;
    let data = {
      mouseWheelDirNum:incrementNum,
      action: 'MOUSE_WHEEL_EVENT',
      cid: '000',
      viewName: this.constructor.name,
      routeDistance: 0,
      routeId,
      isHomeBool,
      paramNum,
      isActive
    };
    this.observer$.next(Object.freeze(data));
  }
  onSubscribeToMediqQueries(p) {
    // let isPortraitPhone = p.media === this.portraitPhoneQueryStr;
    let data = {
      action: 'MEDIA_QUERY_EVENT',
      type: 'phonePortrait',
      matches: p.matches
    };
    this.observer$.next(data);
  }
  onSubscribeToDomEvents(p) {
    let data = {
      action: 'DOM_RESIZE_EVENT',
      data: p
    };
    this.observer$.next(data);
  }
}



